/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
/* RESET */
html :where(.editor-styles-wrapper) select,
html :where(.editor-styles-wrapper) input[type]:not([type=checkbox]),
html :where(.editor-styles-wrapper) textarea,
html :where(.editor-styles-wrapper) button {
  font-family: system-ui;
  -webkit-appearance: revert;
  color: revert;
  border: revert;
  border-radius: revert;
  background: revert;
  box-shadow: revert;
  text-shadow: revert;
  outline: revert;
  cursor: revert;
  outline: none;
  transform: revert;
  font-size: revert;
  line-height: revert;
  padding: revert;
  margin: revert;
  min-height: revert;
  max-width: revert;
  vertical-align: revert;
  font-weight: revert;
}
html :where(.editor-styles-wrapper) input[type]:disabled,
html :where(.editor-styles-wrapper) input[type]:focus,
html :where(.editor-styles-wrapper) select:hover,
html :where(.editor-styles-wrapper) select:focus,
html :where(.editor-styles-wrapper) button {
  color: revert;
  border-color: revert;
  box-shadow: revert;
  text-shadow: revert;
  cursor: revert;
  transform: revert;
}

.wp-core-ui select {
  font-size: revert;
  line-height: revert;
}

/* STYLE */
html :where(.wp-block-formello-form) input[type],
html :where(.wp-block-formello-form) select,
html :where(.wp-block-formello-form) textarea[readonly] {
  background-color: #fff;
  outline: none;
  box-sizing: content-box;
}
html :where(.wp-block-formello-form) input[type]:not([type=checkbox]):not([type=radio]):not([type=color]),
html :where(.wp-block-formello-form) select[readonly],
html :where(.wp-block-formello-form) button {
  min-height: 1em;
  padding: 10px;
  max-width: 100%;
}
html :where(.wp-block-formello-form) input[type=date] {
  padding: 5px;
}
html :where(.wp-block-formello-form) select[readonly]:hover {
  color: #000;
}
html :where(.wp-block-formello-form) button {
  background: #ff7e00;
  color: #000;
  border: none;
  font-size: 1rem;
}

.formello-group .block-editor-block-list__block.wp-block-formello-button {
  margin-top: 0;
}

.formello-urlinput input {
  width: 100% !important;
  border-radius: 2px !important;
  border: 1px solid #757575 !important;
}